<template>
    <CCard class="p-4">
        <CCardBody>
            <form v-on:submit.prevent>
                <h4>Create Rent Payemnt</h4>
                <hr />
                <v-text-field
                    v-model="amount"
                    label="Amount"
                    type="number"
                    :error="errors.amount ? true : false"
                    :error-messages="errors.amount"
                    required
                    outlined
                    dense
                    clearable
                ></v-text-field>
                <v-menu
                    v-model="paymentDateMenu"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    min-width="auto"
                >
                    <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                            v-model="paymentMonth"
                            label="Payment Month"
                            outlined
                            dense
                            clearable
                            readonly
                            :error="errors.payment_month ? true : false"
                            :error-messages="errors.payment_month"
                            v-bind="attrs"
                            v-on="on"
                        ></v-text-field>
                    </template>
                    <v-date-picker
                        v-model="paymentMonth"
                        @input="paymentDateMenu = false"
                        type="month"
                    ></v-date-picker>
                </v-menu>
                <hr />
                <CButton @click="submit" color="primary" class="px-4">
                    Submit
                    <v-progress-circular
                        v-if="loading"
                        indeterminate
                        color="primary"
                        :size="15"
                    ></v-progress-circular>
                </CButton>
            </form>
        </CCardBody>
    </CCard>
</template>

<script>
//

export default {
    name: "CreateRentPayment",
    components: {},
    props: {
        id: null
    },
    data() {
        return {
            amount: "",
            paymentMonth: "",
            paymentDateMenu: false,
            errors: {},
            loading: false
        };
    },
    methods: {
        submit() {
            let self = this;
            if (self.loading) {
                return;
            }
            let data = {
                id: self.$route.params.id,
                amount: self.amount,
                payment_month: self.paymentMonth
            };
            self.loading = true;
            this.$store
                .dispatch("rent/payment/create", data)
                .then(response => {
                    self.loading = false;
                    self.$router.back();
                })
                .catch(error => {
                    self.loading = false;
                });
        }
    }
};
</script>

<style scoped>
.v-text-field >>> input {
    font-size: 0.8em;
    font-weight: 100;
}
.v-text-field >>> label {
    font-size: 0.8em;
}
.v-text-field >>> button {
    font-size: 0.8em;
}
</style>
