           v-model=\"paymentMonth\"\n                        @input=\"paymentDateMenu = false\"\n                        type=\"month\"\n                    ></v-date-picker>\n                </v-menu>\n                <hr />\n                <CButton @click=\"submit\" color=\"primary\" class=\"px-4\">\n                    Submit\n                    <v-progress-circular\n                        v-if=\"loading\"\n                        indeterminate\n                        color=\"primary\"\n                        :size=\"15\"\n                    ></v-progress-circular>\n                </CButton>\n            </form>\n        </CCardBody>\n    </CCard>\n</template>\n\n<script>\n//\n\nexport default {\n    name: \"CreateRentPayment\",\n    components: {},\n    props: {\n        id: null\n    },\n    data() {\n        return {\n            amount: \"\",\n            paymentMonth: \"\",\n            paymentDateMenu: false,\n            errors: {},\n            loading: false\n        };\n    },\n    methods: {\n        submit() {\n            let self = this;\n            if (self.loading) {\n                return;\n            }\n            let data = {\n                id: self.$route.params.id,\n                amount: self.amount,\n                payment_month: self.paymentMonth\n            };\n            self.loading = true;\n            this.$store\n                .dispatch(\"rent/payment/create\", data)\n                .then(response => {\n                    self.loading = false;\n                    self.$router.back();\n                })\n                .catch(error => {\n                    self.loading = false;\n                });\n        }\n    }\n};\n</script>\n\n<style scoped>\n.v-text-field >>> input {\n    font-size: 0.8em;\n    font-weight: 100;\n}\n.v-text-field >>> label {\n    font-size: 0.8em;\n}\n.v-text-field >>> button {\n    font-size: 0.8em;\n}\n</style>\n"],"sourceRoot":""}]);
